@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

/*add font*/
@layer base {
    body {
        @apply font-[Raleway];
        padding: 0;
        margin: 0;
    }
    li {
        @apply px-4;
        @apply cursor-pointer;
    }
}